<template>
  <div class="item-container" :class="[typeClass(_item.type)]">
    <div><img class="icon-img" :src="_item.iconUrl"></div>
    <div class="item-name">{{_item.name}}</div>
    <mt-button
      :disabled="_item.type===-1"
      class="item-button"
      plain
      type="primary"
      @click="buy">
      <i class="icon-ls iconfont icon-taojinbi"></i>
      {{_item.gold}}
    </mt-button>
  </div>
</template>

<script>
  import Vue from 'vue'
  import {Component, Prop} from 'vue-property-decorator'
  import { Getter, Action } from 'vuex-class'
  import { MessageBox, Toast } from 'mint-ui'

  @Component
  export default class ItemBlock extends Vue {
    @Prop(Object) item

    @Getter('getUserInfo') userInfo
    @Action('updateBag') updateBag
    @Action('updateGold') updateGold

    get _item () {
      if (!this.item) return {
        name: '新货未到',
        type: -1,
        gold: '???'
      };
      return this.item
    }

    typeClass (type) {
      switch(type) {
        case 0: return 'mini';
        case 1: return 'small';
        case 2: return 'middle';
        case 3: return 'high';
        case 4: return 'higher';
      }
    }

    buy () {
      MessageBox({
        title: '提示',
        message: `确认购买【${this.item.name}】`,
        showCancelButton: true
      }).then(action => {
        if (action === 'confirm') {
          if (this.userInfo.gold < this.item.gold) {
            return Toast('灵石不足')
          }
          this.updateBag([
            {
              id: this.item.id,
              num: 1
            }
          ])
          this.updateGold(-this.item.gold)
          Toast('购买成功')
        }
      })
    }
  }
</script>

<style scoped lang="scss">
  .item-container {
    display: inline-block;
    width: 100px;
    text-align: center;
  }
  .icon-ls {
    color: #67C23A;
    margin-right: 5px;
  }
  .icon-img {
    width: 50px;
    height: 50px;
    border: 2px solid #DCDFE6;
    border-radius: 8px;
  }
  .item-button {
    height: 30px;
  }
  .icon-img {
    border-color: #909399;
  }
  .item-name {
    color: #909399;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
  }
  .mini {
    .icon-img {
      border-color: #606266;
    }
    .item-name {
      color: #606266;
    }
  }
  .small {
    .icon-img {
      border-color: #67C23A;
    }
    .item-name {
      color: #67C23A;
    }
  }
  .middle {
    .icon-img {
      border-color: #409eff;
    }
    .item-name {
      color: #409eff;
    }
  }
  .high {
    .icon-img {
      border-color: #F56C6C;
    }
    .item-name {
      color: #F56C6C;
    }
  }
  .higher {
    .icon-img {
      border-color: #E6A23C;
    }
    .item-name {
      color: #E6A23C;
    }
  }
</style>


