<template>
  <div>
    <mt-header fixed title="能力">
      <router-link to="/my" slot="left">
        <mt-button icon="back">返回</mt-button>
      </router-link>
    </mt-header>
    <div class="mt-header-content content-container">
      <Tabs
        class="content-tabs"
        :tabs="tabs"
        @click="active"></Tabs>
      <TalentBlock
        v-for="(talent, index) in talents"
        :key="index"
        :item="talent"></TalentBlock>
    </div>
  </div>
</template>

<script>
  import Vue from 'vue'
  import {Component} from 'vue-property-decorator'
  import Tabs from '@/components/tabs/index.vue'
  import TalentBlock from './talentBlock.vue'
  import config from './talentConfig'

  @Component({
    components: {
      Tabs,
      TalentBlock
    }
  })
  export default class Talent extends Vue {
    tabs = [
      {
        label: '瞳术',
        id: 'ts'
      },
      {
        label: '恶魔果实',
        id: 'emgs'
      }
    ]

    activeTab = 'ts'

    get talents () {
      return config[this.activeTab]
    }

    active(tab) {
      this.activeTab = tab.id
    }
  }
</script>

<style scoped>
  .content-container {
    padding-top: 43px;
  }
  .content-tabs {
    position: absolute;
    top: 40px;
    left: 0;
    right: 0;
  }
</style>


