<template>
  <div>
    <mt-header fixed title="商店">
      <router-link to="/otherApps" slot="left">
        <mt-button icon="back">返回</mt-button>
      </router-link>
    </mt-header>
    <div class="mt-header-content shop-content">
      <ItemBlock
        class="item-block"
        v-for="index in 12"
        :key="index"
        :item="items[index-1]"></ItemBlock>
    </div>
  </div>
</template>

<script>
  import Vue from 'vue'
  import {Component} from 'vue-property-decorator'
  import ItemBlock from './itemBlock.vue'

  @Component({
    components: {
      ItemBlock
    }
  })
  export default class index extends Vue {
    items = [
      {
        name: '火精石',
        id: 'ljs',
        gold: 88,
        type: 0
      },
      {
        name: '苍瞳咒印',
        id: 'ctzy',
        gold: 188,
        type: 1
      },
      {
        name: '赤瞳咒印',
        id: 'chtzy',
        gold: 688,
        type: 2
      }
    ]
  }
</script>

<style scoped>
.shop-content {
  text-align: center;
}
  .item-block {
    margin: 10px 0;
  }
</style>


