<template>
  <div class="tabs-container">
    <span
      v-for="(tab, index) in tabs"
      :class="{isActive: activeItem===tab.id}"
      class="tabs-item"
      @click="handleClick(tab)">{{tab.label}}</span>
  </div>
</template>

<script>
  import Vue from 'vue'
  import { Component, Prop } from 'vue-property-decorator'

  @Component
  export default class index extends Vue {
    @Prop(Array) tabs;

    activeItem = ''

    handleClick (tab) {
      this.activeItem = tab.id;
      this.$emit('active', tab)
    }

    created () {
      this.activeItem = this.tabs[0].id
    }
  }
</script>

<style scoped>
  .tabs-container {
    white-space: nowrap;
    overflow: auto;
    border-bottom: 1px solid #DCDFE6;
    color: #909399;
    background-color: #FFF;
    padding: 0 10px;
  }

  .tabs-item {
    display: inline-block;
    height: 40px;
    line-height: 40px;
    margin: 0 10px;
  }

  .isActive {
    border-bottom: 2px solid #409EFF;
    color: #409EFF
  }
</style>


