import Vue from 'vue'
import { types } from './mutations';
import { saveUserInfo } from '../../api/userInfo'

const actions = {
  // 修改金币
  updateGold (context, payload) {
    let userInfo = context.state.userInfo;
    if (!userInfo.gold) {
      userInfo.gold = 0;
    }
    userInfo.gold += payload;
    context.commit(types.UPDATE_USER_INFO, userInfo);
    saveUserInfo(userInfo)
  },
  // 修改背包
  updateBag (context, payload) {
    let userInfo = context.state.userInfo;
    if (!userInfo.bag) {
      Vue.set(userInfo, 'bag', {})
    }
    let bag = userInfo.bag;
    payload.forEach(ele => {
      if (!bag[ele.id]) {
        Vue.set(bag, ele.id, {});
        bag[ele.id].num = 0
      }
      bag[ele.id].num += ele.num
    });
    context.commit(types.UPDATE_USER_INFO, userInfo);
    saveUserInfo(userInfo)
  },
  updateTalent (context, id) {
    let userInfo = context.state.userInfo;
    if (!userInfo.talents) {
      Vue.set(userInfo, 'talents', {})
    }
    if (!userInfo.talents[id]) {
      Vue.set(userInfo.talents, id, {
        level: -1
      })
    }
    userInfo.talents[id].level++;
    context.commit(types.UPDATE_USER_INFO, userInfo);
    saveUserInfo(userInfo);
    console.log(userInfo)
  }
};

export default actions;



