<template>
  <div class="talent-block">
    <div class="img-container">
      <img class="icon-img" :src="current.iconUrl">
      <mt-button
        v-if="isNext"
        :disabled="!provable"
        plain
        :class="{'mint-button--success': provable}"
        type="danger"
        size="small"
        @click="proveLevel">进阶</mt-button>
    </div>
    <div class="info">
      <div class="info-item" :class="[textClass]">{{current.name}}<span v-if="current.level===-1">【未激活】</span></div>
      <div class="info-item" :class="[textClass]">效果：战斗力+{{current.zdl}}</div>
      <div class="text_danger info-item" v-if="isNext">下阶段：【{{current.nextName}}】战斗力+{{current.nextZdl}}</div>
      <div class="text_success info-item" v-if="!isNext">已至顶阶</div>
      <div class="info-item" v-if="isNext">
        <span :class="[provable ? 'text_success' : 'text_danger']">进阶材料：
          <span v-for="(m, index) in current.material" :key="index">
            {{m.name}}({{m.num}}/{{hasMateralNum(m.id)}})
          </span>
        </span>
      </div>
    </div>
  </div>
</template>

<script>
  import Vue from 'vue'
  import {Component, Prop} from 'vue-property-decorator'
  import { Getter, Action } from 'vuex-class'
  import { Toast } from 'mint-ui'

  @Component
  export default class TalentBlock extends Vue {
    @Prop(Object) item
    @Getter('getUserInfo') userInfo
    @Action('updateTalent') updateTalent
    @Action('updateBag') updateBag

    get current () {
      let userTalents = this.userInfo.talents
      let userTalent = userTalents && userTalents[this.item.id]
      if (!userTalent) {
        return {
          name: this.item.items[0].name,
          iconUrl: this.item.items[0].iconUrl,
          zdl: 0,
          nextZdl: this.item.items[0].zdl,
          material: this.item.items[0].material,
          level: -1,
          nextName: this.item.items[0].name
        }
      }
      let level = userTalent.level
      let talentConfig = this.item.items[level]
      let nextTalentConfig = this.item.items[++level]
      return {
        name: talentConfig.name,
        iconUrl: talentConfig.iconUrl,
        zdl: talentConfig.zdl,
        nextZdl: !!nextTalentConfig ? nextTalentConfig.zdl : -1,
        material: nextTalentConfig && nextTalentConfig.material,
        level: level,
        nextName: nextTalentConfig && nextTalentConfig.name
      }
    }

    get textClass () {
      let level = this.current.level
      if (level === -1) {
        return 'text_info'
      }
      if (level === 1) {
        return 'text_success'
      }
      if (level === 2) {
        return 'text_primary'
      }
      if (level === 3) {
        return 'text_warning'
      }
      return 'text_danger'
    }

    get isNext () {
      return this.current.nextZdl !== -1
    }

    get provable () {
      for (let i = 0; i < this.current.material.length; ++i) {
        let ele = this.current.material[i]
        if (this.hasMateralNum(ele.id) < ele.num) {
          return false
        }
      }
      return true
    }

    hasMateralNum (id) {
      let bag = this.userInfo.bag || {}
      return !!bag[id] ? bag[id].num : 0
    }

    proveLevel () {
      if (!this.isNext) {
        return Toast('您已修炼至顶阶，无需进阶')
      }
      let bagUpadateConfig = []
      for (let i = 0; i < this.current.material.length; ++i) {
        let ele = this.current.material[i]
        if (this.hasMateralNum(ele.id) < ele.num) {
          return Toast('材料不足')
        }
        bagUpadateConfig.push({
          id: ele.id,
          num: -ele.num
        })
      }
      this.updateBag(bagUpadateConfig)
      this.updateTalent(this.item.id)
    }
  }
</script>

<style scoped>
  .talent-block {
    position: relative;
    padding: 10px;
    margin: 10px 0;
    background-color: #FFF;
    border-radius: 5px;
    box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
  }

  .img-container {
    width: 60px;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    text-align: center;
  }

  .icon-img {
    width: 60px;
    height: 60px;
    border-radius: 10px;
  }

  .info {
    vertical-align: top;
    margin-left: 80px;
  }
  .info-item {
    min-height: 30px;
  }
</style>
<style lang="scss">
  .talent-block {
    .mint-button--small {
      height: 28px;
    }
  }
</style>


